import { createCustomElement } from '@servicenow/ui-core';

import react from '@servicenow/ui-renderer-react';
import { transformState, ForwardProperties } from '@devsnc/sn-controls-common';

import NowTime from './NowTime';

export default NowTime;

/**
 *
 * Time component allowing users to select a time
 * Can be in 24 hour format or 12 hour format (AM|PM)
 * Can specify if hours/minutes/seconds are included
 *
 * ```jsx
 * <now-record-time name="time" value="12:30:30 am" format="hh:mm:ss a" />
 * ```
 *
 * 24 hour format
 *
 * ```jsx
 * <now-record-time
 *     label="24-hour format"
 *     name="time-24-hours"
 *     value="18:45:00"
 *     format="HH:mm:ss"
 * />
 * ```
 *
 * @seismicElement now-record-time
 * @summary Time component allowing users to select a time that can be in either 24 hour or 12 hour (AM|PM) format. The user can also specify the hh:mm:ss format.
 *
 * */

createCustomElement('now-record-time', {
	type: react,
	view: ForwardProperties(NowTime),
	styles: NowTime.style,
	properties: {
		/**
		 * Causes the component to receive focus as soon as it has finished
		 * mounting.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		autofocus: {},
		/**
		 * When true, the time field will always default to 00:00:00 when empty
		 * @type {boolean}
		 */
		defaultZeros: { default: false },
		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 * @type {string}
		 */
		description: {},
		/**
		 * How to serialize/deserialize the date value.
		 * @see [Moment format strings](https://momentjs.com/docs/#/displaying/format/)
		 * @type {string}
		 */
		format: {},
		/**
		 * This is what the user sees between each time unit. If you used a `.`
		 * the time would appear as `03.00.00` instead of `03:00:00`.
		 * @type {string}
		 */
		separator: { default: ':' },
		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		invalid: { default: false },
		/**
		 * It's a label! All form controls need a label!
		 * @type {string}
		 */
		label: {},
		/**
		 * User's preferred language
		 * @type {string}
		 */
		language: { default: 'en' },
		/**
		 * Array of message objects that must contain type and message itself.
		 * @type {Array<{type: ("error"|"warning"|"success"|"liveUpdate"|"info"|"suggestion"), type:Array<string,Object<{string, getMessage:function}>> }>}
		 */
		messages: { default: [] },
		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 * @type {string}
		 */
		name: {},
		/**
		 * Callback used when an intermediate value has changed.
		 * @type {function}
		 * @private
		 */
		onStagedValueChange: {},
		/**
		 * Callback used when the value has changed. Very similar to an `onChange`
		 * event, but this may also fire when the component formats its value
		 * onBlur.
		 *
		 * Params
		 *
		 * `type:object` event - The event payload
		 *
		 * `type:string` event.name - The name of the component that triggered the event
		 *
		 * `type:any` event.value - The new value for the component
		 *
		 * `type:string` event.displayValue - The human-readable represenation of the value
		 *
		 * `type:object` event.error - If the current value is invalid, this propery will be set. When the current value is valid, this property will be null.
		 *
		 * `type:string` event.error.type - Error type
		 *
		 * `type:string` event.error.message - Description of why the value is invalid
		 *
		 * @type {function}
		 * @private
		 */
		onValueChange: {},
		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {boolean}
		 */
		readonly: { default: false },
		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {boolean}
		 */
		required: { default: false },
		/**
		 * Used with TimeAgo Presets like 'Last 15 minutes'
		 * When true, this component will not update the default time values
		 */
		relativeTime: { default: false },
		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {boolean}
		 */
		reversed: { default: false },
		/**
		 * When true, this component uses 24-hour based notation rather than 12-hour. If
		 * this prop is not specified, a default is inferred based on the format string
		 * @type {boolean}
		 */
		use24Hour: {},
		/**
		 * Underlying data for the component.
		 * @type {string}
		 */
		value: {}
	},
	dispatches: {
		/**
		 * Dispatched when a different time is inputted before you blur
		 * @type {{ value: string }}
		 */
		'NOW_RECORD_TIME#STAGED_VALUE_CHANGED': {},
		/**
		 * Dispatched when the user blurs off of the component
		 * @type {{ value: string, error: { type: string, message: string } }}
		 */
		'NOW_RECORD_TIME#VALUE_CHANGED': {}
	},
	transformState
});
