import React from 'react';
import PropTypes from 'prop-types';
import { padStart, map, mapValues, clamp, reduce, keys, inRange } from 'lodash';
import moment from 'moment';
import { t } from 'sn-translate';
import '@devsnc/sn-record-dropdown-internal';
import { Input, SNInputGroup } from '@devsnc/sn-record-input';

import {
	FocusWatcher,
	isAttrTrue,
	accessibilityAttributes,
	fieldPropTypes,
	withValueChangeCallbacks
} from '@devsnc/sn-controls-common';

import style from './time.scss';

// TODO: Probs merge some of the common time stuff with
// Duration element.
// Also, some of the AM/PM hour handling is kinda ugly.
// Could probs be cleaned up a bit.

// Unit names and limiting values. Each key+value generates
// an input field, with the key name used as the input label.
export const UNIT_LIMITS = {
	hours: [0, 24],
	minutes: [0, 60],
	seconds: [0, 60]
};

export const UNIT_NAMES = Object.keys(UNIT_LIMITS);
const DISPLAY_UNIT_NAMES = [t('hours'), t('minutes'), t('seconds')];

/**
 * Convert a 12-hour value into a 24-hour value
 */
export function to24Hour(value, isPM) {
	if (value === 0 || value === '') {
		return 0;
	}

	value = clamp(value, 1, 12);

	if (value === 12) {
		return isPM ? 12 : 0;
	}

	if (isPM) {
		value += 12;
	}

	return isNaN(value) ? 0 : value;
}

/**
 * Breaks out a moment time object into separate formatted values
 */
export function getTimeSegments(time, use24Hour) {
	const isValid = time.isValid();
	return mapValues(UNIT_LIMITS, (limit, unitName) => {
		if (!isValid) {
			return '';
		}

		const rawValue =
			!use24Hour && unitName === 'hours'
				? time.format('h')
				: time.get(unitName);
		return padStart(rawValue, 2, '0');
	});
}

/**
 * A group of inputs to select a specific time of day.
 */
export class NowTime extends React.PureComponent {
	static componentName = 'now-record-time';
	static style = style;

	static defaultProps = {
		format: 'hh:mm:ss a',
		relativeTime: false
	};

	constructor(props) {
		super(props);

		//validate incoming value
		const isValidValue = moment(props.value, props.format, true).isValid();
		const initialTime = this.createTime(props);
		this.state = {
			...getTimeSegments(initialTime, this.use24Hour()),
			pm: initialTime.hours() >= 12,
			hasFocus: false
		};
		if (!isValidValue && props.value)
			props.onValueChange({
				value: props.value,
				error: {
					type: 'error',
					message: t('{0} is not a valid time', props.value)
				}
			});
	}

	/**
	 * Boolean indicating whether or not this component should use 24-hour format.
	 * Checks for an explicity `use24Hour` prop, or when that isn't there it infers a value
	 * based on the format string
	 */
	use24Hour(props = this.props) {
		return props.use24Hour === undefined
			? String(props.format).includes('H')
			: isAttrTrue(props.use24Hour);
	}

	// only include am/pm toggle when using 12-hour format if it includes the am/pm `a` in the format
	// the existing platform behaves the same way where it only allows am values if no `a` is provided`
	useAmPm = (props = this.props) =>
		!this.use24Hour(props) && /\ba\b/.test(props.format);

	showSeconds(props = this.props) {
		return String(props.format).includes('s');
	}

	onToggleAmPm = ({ value }) => {
		this.setState(
			() => ({ pm: value === 'pm' }),
			this.state.hours ? this.onGroupBlur : undefined
		);
	};

	isTimeValid = (state = this.state) => {
		return reduce(
			keys(UNIT_LIMITS),
			(result, value) => result && inRange(state[value], ...UNIT_LIMITS[value]),
			true
		);
	};

	/**
	 * Create a moment time object.
	 *
	 * Can be an invalid time when value is empty string
	 */
	createTime = (props, state) => {
		let initialTime = moment(props.value, props.format, true);
		if (state) {
			let { hours, minutes, seconds } = state;
			hours = this.use24Hour()
				? hours
				: hours > 12
				? hours
				: to24Hour(hours, state.pm);
			initialTime.set({ hours, minutes, seconds });
		}
		return initialTime;
	};

	getTimeString(props, state) {
		let initialTime = this.createTime(props, state);
		if (!initialTime.isValid()) {
			let { hours, minutes, seconds } = state;
			hours = this.use24Hour()
				? hours
				: hours > 12
				? hours
				: to24Hour(hours, state.pm);
			initialTime = moment()
				.startOf('day')
				.set({ hours, minutes, seconds });
		}
		return initialTime.format(props.format);
	}

	onChange = event => {
		event.persist();

		this.setState(state => {
			return {
				...state,
				[event.target.name]: event.target.value
			};
		});
	};

	onGroupBlur = event => {
		if (!this.props.relativeTime) {
			this.setState(state => {
				const isValidTime = this.isTimeValid(state);
				if (
					(!state.hours || !isValidTime) &&
					(state.minutes || state.seconds)
				) {
					this.props.onValueChange({
						event,
						value: this.props.value,
						error: {
							type: 'error',
							message: t(
								'{0} is not a valid time',
								`${state.hours}:${state.minutes}:${state.seconds}`
							)
						}
					});

					return state;
				}

				let value = '';
				if (state.hours || this.props.defaultZeros)
					value = this.getTimeString(this.props, state);

				this.props.onValueChange({
					event,
					value
				});

				return {
					...getTimeSegments(
						this.createTime(this.props, state),
						this.use24Hour()
					),
					hasFocus: false
				};
			});
		}
	};

	onBlur = event => {
		if (
			event &&
			event.target &&
			event.target.name !== 'hours' &&
			this.state.hours &&
			!this.state[event.target.name]
		)
			this.setState({
				[event.target.name]: '00'
			});
	};

	onFocus = () => {
		this.setState({
			hasFocus: true
		});
	};

	componentWillReceiveProps(nextProps) {
		if (nextProps.value !== this.props.value && !this.state.hasFocus) {
			const newTime = this.createTime(nextProps);
			const timeSegments = getTimeSegments(newTime, this.use24Hour(nextProps));
			this.setState({
				...timeSegments,
				pm: newTime.hours() >= 12
			});
		}
	}

	static items = [
		{ id: 'am', label: t('AM') },
		{ id: 'pm', label: t('PM') }
	];

	static am = ['am'];
	static pm = ['pm'];

	render() {
		const readonly = isAttrTrue(this.props.readonly);
		const { separator } = this.props;
		return (
			<div>
				<div className="sn-timepicker">
					<SNInputGroup {...this.props}>
						<FocusWatcher onBlur={this.onGroupBlur} persistant={true}>
							{setFocusTarget => (
								<React.Fragment>
									{map(UNIT_NAMES, (unitName, index) => {
										const isFirstInput = index === 0;

										if (!this.showSeconds() && unitName === 'seconds') {
											return;
										}

										const ariaLabel = t(
											'{0} {1}',
											this.props.label || '',
											DISPLAY_UNIT_NAMES[index]
										).trim();

										return (
											<React.Fragment key={unitName}>
												{!isFirstInput && (
													<span className="sn-timepicker-separator">
														{separator || ':'}
													</span>
												)}
												<Input
													{...accessibilityAttributes(this.props)}
													// omit aria-labelledby since we have an explicit aria-label here
													// and screenreaders take aria-labelledby over aria-label
													aria-labelledby={undefined}
													componentId={`${this.props.componentId}-${unitName}`}
													aria-label={ariaLabel}
													readonly={this.props.readonly}
													invalid={this.props.invalid}
													name={unitName}
													value={this.state[unitName]}
													autofocus={index === 0 && this.props.autofocus}
													onFocus={this.onFocus}
													onBlur={this.onBlur}
													onChange={this.onChange}
													innerRef={node => setFocusTarget(unitName)(node)}
												/>
											</React.Fragment>
										);
									})}
									{this.useAmPm() && (
										<div className="sn-timepicker-toggle">
											<sn-record-dropdown-internal
												bare
												on-change={this.onToggleAmPm}
												selected-items={this.state.pm ? NowTime.pm : NowTime.am}
												disabled={readonly}
												variant="secondary"
												size="md"
												items={NowTime.items}
											/>
										</div>
									)}
								</React.Fragment>
							)}
						</FocusWatcher>
					</SNInputGroup>
				</div>
			</div>
		);
	}
}

NowTime.propTypes = {
	...fieldPropTypes,

	/**
	 * How to serialize/deserialize the date value.
	 * @see [Moment format strings](https://momentjs.com/docs/#/displaying/format/)
	 */
	format: PropTypes.string,

	/**
	 * This is what the user sees between each time unit. If you used a `:` the time would appear as `03:00:00`.
	 */
	separator: PropTypes.string,

	/**
	 * When true, this component uses 24-hour based notation rather than 12-hour. If
	 * this prop is not specified, a default is inferred based on the format string
	 */
	use24Hour: PropTypes.bool,

	/**
	 * Used with TimeAgo Presets like 'Last 15 minutes'
	 * When true, this component will not update the default time values
	 */
	relativeTime: PropTypes.bool,

	/**
	 * When true, the time field will always default to 00:00:00 when empty
	 * This fixes PRB1348218 because in Date/Time fields, we want to have default
	 * values that way we won't clear out the entire input value when only the
	 * HH:MM:SS portion is left empty
	 */
	defaultZeros: PropTypes.bool
};

export default withValueChangeCallbacks(
	{ stopBubbling: ['SN_RECORD_INPUT'] },
	NowTime
);
